import React, { Component } from 'react';
import CloseMainMenu from './CloseMainMenu';
import Reset from './Reset';
import Scroll from '../components/Scroll.js';


// import { upgrades } from './upgrades.js';

// Import Css
import './mainMenu.css';


class MainMenu extends Component {
    constructor(){
        super()       
        this.state = {
            isOpen: false,
        }


}

openMenu=()=>{
    this.setState({
        isOpen: true
    });
}

closeMainMenu=()=>{
    console.log('close Main Menu');
    this.setState((state) => ({ isOpen: false}));
}

render()   {
    // destructure
    const { isOpen } = this.state;
    const { closeMainMenu } = this;
    
    if (isOpen === true){
        return(
        <div id="mainMenu">
            <h1 >Main Menu</h1>
            <h2>Welcome to Idle Billionare</h2>
            <Scroll>
                
                <h3>Game Rules</h3>
                <p>- reach a billion dollars or beyond</p>
                <p>- earn $ and happy to buy cards that increase the rate you earn $ and happy</p>
                <p>- watch out you don't get -10,000 happy...</p>
                <p>- you lose if your balance reaches -$100,000.</p>
                <p>I hope this app can offer some perspective into how big a number 1 billion really is</p>
                <h3>About</h3>
                <p>by The Slanted Room</p>
                
                <br></br>
                <p>This app is currently in active development</p>
                <p>as I continue to add new cards, older save files may not reflect the new data correctly</p>
                <br></br>

                <h3>Credit</h3>
                <p>"Cats Images by David Revoy by way of Robohash"</p>
                <h3>Links</h3>
                <a href="https://theslantedroom.github.io/Last-Man-Standing-Mini-Game/" target="_blank">Last Man Standing - Mini Game</a>
                <br></br>
                <a href="https://theslantedroom.github.io/steve.yee/" target="_blank">The Slanted Room</a>
               
                
            </Scroll>
            <CloseMainMenu id="mainMenuClose" closeMainMenu = {closeMainMenu}/>
            <Reset resetButton = {this.props.resetButton}/>
        </div>  
        );   
    } else {
        return(
            <div>

            </div>     
            ); 
        }
    }
}


export default MainMenu; 

